import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import focusBehavior from '@servicenow/behavior-focus';
import rtlBehavior from '@servicenow/behavior-rtl';
import resizeBehavior from '@servicenow/behavior-resize';
import '@servicenow/now-tooltip';
import '@servicenow/now-collapse';
import '@servicenow/now-icon';
import isObject from 'lodash/isObject';

import styles from './_now-accordion-item.scss';
import {triggerIconSchema, headingLevelSchema} from './schemas';

const {COMPONENT_RENDERED} = actionTypes;
const labelVariants = ['primary', 'secondary'];
const labelSizes = ['sm', 'md', 'lg'];

const getAltIcon = (expanded) => (expanded ? 'minus-fill' : 'plus-fill');
const isTruncated = (element) => element.offsetWidth < element.scrollWidth;

const getHeaderValues = (header) => {
	let weight,
		size = 'md',
		variant = 'primary',
		label = header;

	if (isObject(header)) {
		label = header.label;
		weight = header.weight;
		size = labelSizes.includes(header.size) ? header.size : size;
		variant = labelVariants.includes(header.variant) ? header.variant : variant;
	}

	return {weight, size, variant, label};
};

const getCaptionValues = (caption) => {
	let style,
		variant = 'primary',
		label = caption;

	if (isObject(caption)) {
		label = caption.label;
		style = caption.style;
		variant = labelVariants.includes(caption.variant)
			? caption.variant
			: variant;
	}

	return {style, variant, label};
};

const renderTriggerIcon = (type, expanded, disabled) => (
	<now-icon
		class={{
			'now-accordion-item-button-icon': true,
			'is-disabled': disabled,
			'is-expanded': expanded
		}}
		icon={type === 'plus-minus' ? getAltIcon(expanded) : 'chevron-down-fill'}
	/>
);

const renderCaption = (caption, disabled) => {
	if (!caption || (isObject(caption) && !caption.label)) return;

	const {label, style, variant} = getCaptionValues(caption);

	return (
		<span
			class={{
				'now-accordion-item-caption': true,
				['-' + variant]: true,
				'is-italic': style === 'italic',
				'is-disabled': disabled
			}}>
			<span className="now-line-height-crop">
				<span className="now-accordion-item-label" aria-label={`${label}.`}>
					{label}
				</span>
			</span>
		</span>
	);
};

const renderPrimaryLabel = (header, disabled) => {
	const {weight, size, variant, label} = getHeaderValues(header);

	return (
		<span
			id="accordion-trigger-primary-label"
			class={{
				'now-accordion-item-header': true,
				['-' + variant]: true,
				['-' + size]: true,
				'is-bold': weight === 'bold',
				'is-disabled': disabled
			}}>
			<span className="now-line-height-crop">
				<span className="now-accordion-item-label" aria-label={`${label}.`}>
					{label}
				</span>
			</span>
		</span>
	);
};

const renderLabels = (header, caption, disabled) => (
	<span className="now-accordion-item-labels">
		{renderPrimaryLabel(header, disabled)}
		{renderCaption(caption, disabled)}
	</span>
);

const renderTrigger = (expanded, triggerIcon, header, caption, disabled) => {
	const {type = 'chevron', position = 'end'} = triggerIcon;

	return (
		<button
			id="accordion-trigger"
			aria-controls="accordion-item"
			aria-expanded={expanded ? 'true' : 'false'}
			disabled={disabled}
			class={{'now-accordion-item-button': true, 'is-disabled': disabled}}>
			{position === 'start'
				? renderTriggerIcon(type, expanded, disabled)
				: undefined}
			<slot name="identifier" />
			{renderLabels(header, caption, disabled)}
			<slot name="metadata" />
			{position === 'end'
				? renderTriggerIcon(type, expanded, disabled)
				: undefined}
		</button>
	);
};

const renderTooltips = ({headerRef, captionRef}, header, caption, disabled) => {
	if (disabled) return;

	return (
		<Fragment>
			{headerRef && isTruncated(headerRef) ? (
				<now-tooltip
					id={`tooltip-header`}
					target-ref={headerRef}
					content={header}
				/>
			) : (
				undefined
			)}
			{captionRef && isTruncated(captionRef) ? (
				<now-tooltip
					id={`tooltip-caption`}
					target-ref={captionRef}
					content={caption}
				/>
			) : (
				undefined
			)}
		</Fragment>
	);
};

const renderCollapse = (expanded) => (
	<now-collapse
		aria-labelledby="accordion-trigger-primary-label"
		id="accordion-item"
		expanded={expanded}>
		<slot name="content" />
	</now-collapse>
);

const view = (state) => {
	const {
		expanded,
		headingLevel,
		header,
		caption,
		triggerIcon,
		dividers,
		disabled
	} = state.properties;
	const {headerRef, captionRef} = state;

	const isExpanded = disabled ? false : expanded;
	const renderDivider = () => (
		<hr
			class={{'now-m-block--0': true, 'is-disabled': disabled}}
			aria-hidden="true"
		/>
	);
	const Header = headingLevel ? `h${headingLevel}` : `div`;

	return (
		<Fragment>
			{dividers.top ? renderDivider() : undefined}
			<Header data-testid="header" className="now-m-block--0">
				{renderTrigger(isExpanded, triggerIcon, header, caption, disabled)}
			</Header>
			{renderCollapse(isExpanded)}
			{dividers.bottom ? renderDivider() : undefined}
			{renderTooltips(
				{headerRef, captionRef},
				isObject(header) ? header.label : header,
				isObject(caption) ? caption.label : caption,
				disabled
			)}
		</Fragment>
	);
};

/**
 * A `now-accordion` sub-component that shows and hides a single accordion item.
 *
 * Simple example:
 *
 * ```jsx
 * <now-accordion expand-single>
 * 	<now-accordion-item header=”Toggle 1”>
 *		<p slot="content">Slotted content!</p>
 * 	</now-accordion-item>
 * 	<now-accordion-item header=”Toggle 2” caption="(expanded" expanded>
 *		<p slot="content">More slotted content!</p>
 * 	</now-accordion-item>
 * </now-accordion>
 * ```
 *
 * Complex example (one item):
 *
 * ```jsx
 * <now-accordion
 * 	expand-single
 * 	hide-dividers
 * 	heading-level="2"
 * 	trigger-icon={{type: 'plus-minus', position: 'start'}}>
 * 	<now-accordion-item
 * 		expanded
 * 		header={{
 * 			label: 'Toggle first item (click)',
 * 			weight: 'bold',
 * 			size: 'sm',
 * 			variant: 'secondary'
 * 		}}
 * 		caption={{label: 'Sub-label 1', style: 'italic', variant: 'primary'}}>
 * 		<now-icon slot="identifier" icon="building-outline" />
 * 		<em slot="metadata">2 of 3</em>
 * 		<p slot="content">Default slotted content</p>
 * 	</now-accordion-item>
 * </now-accordion>
 * ```
 *
 * @seismicElement now-accordion-item
 * @summary A `now-accordion` sub-component that shows and hides an item of content.
 * @uib.label Accordion Item
 * @uib.icon accordion-item
 * @uib.description Accordion item to use within an accordion
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties header, caption, disabled, expanded
 */
createCustomElement('now-accordion-item', {
	properties: {
		/**
		 * Configures the `header` for the item trigger.
		 * Truncates at narrow widths and includes a tooltip on hover.
		 *
		 * If only a label is provided, the component uses the default values below
		 * for variant, size, and weight.
		 *
		 * Object options:
		 * - `[header.label]`: String. Required. The header label.
		 * - `[header.variant = 'primary']`: String. Variant style of the header.
		 * - `[header.size = 'md']`: String. Font size of the header.
		 * - `[header.weight = 'normal']`: String. Font weight of the header.
		 * @type {{label: string, variant: ('primary'|'secondary'), weight: ('normal'|'bold'), size: ('sm'|'md'|'lg')}}
		 * @uib.description Configures the header for the item. Truncates at narrow widths and includes a tooltip on hover.
		 * @uib.defaultValue {"label": "Heading Label", "variant": "primary", "weight": "normal, "size": "md"}
		 * @uib.fieldType json
		 */
		header: {
			required: true,
			schema: {
				oneOf: [
					{type: 'string'},
					{
						type: 'object',
						properties: {
							label: {type: 'string'},
							variant: {type: 'string', enum: labelVariants},
							size: {type: 'string', enum: labelSizes},
							weight: {type: 'string', enum: ['normal', 'bold']}
						},
						additionalProperties: false,
						required: ['label']
					}
				]
			}
		},
		/**
		 * Configures a `sub-label` below the primary trigger label.
		 * Truncates at narrow widths and includes a tooltip on hover.
		 *
		 * If only a label is provided, the component uses the default values below
		 * for variant and style.
		 *
		 * Object options:
		 * - `[caption.label]`: String. Required. The caption label.
		 * - `[caption.variant = 'primary']`: String. Variant style of the sub-label.
		 * - `[caption.style = 'normal']`: String. Font style variant of the sub-label.
		 * @type {{label: string, variant: ('primary'|'secondary'), style: ('normal'|'italic')}}
		 * @uib.description Configures a `sub-label` below the primary trigger label. Truncates at narrow widths and includes a tooltip on hover.
		 * @uib.defaultValue {"label": "Caption Label", "variant": "primary", "style": "normal"}
		 * @uib.fieldType json
		 */
		caption: {
			schema: {
				oneOf: [
					{type: 'string'},
					{
						type: 'object',
						properties: {
							label: {type: 'string'},
							variant: {type: 'string', enum: labelVariants},
							style: {type: 'string', enum: ['normal', 'italic']}
						},
						additionalProperties: false,
						required: ['label']
					}
				]
			}
		},
		/**
		 * Disables the accordion.
		 *
		 * Default: `false`
		 * @type {boolean}
		 * @uib.description Disables the accordion item
		 */
		disabled: {default: false, schema: {type: 'boolean'}},
		/**
		 * Expands the accordion.
		 *
		 * Default: `false`
		 * @type {boolean}
		 * @uib.description Expands the accordion item
		 */
		expanded: {default: false, schema: {type: 'boolean'}},
		/**
		 * @private
		 */
		headingLevel: {schema: headingLevelSchema},
		/**
		 * @private
		 */
		triggerIcon: {
			default: {type: 'chevron', position: 'end'},
			schema: triggerIconSchema
		},
		/**
		 * @private
		 */
		dividers: {
			default: {},
			schema: {
				type: 'object',
				properties: {top: {type: 'boolean'}, bottom: {type: 'boolean'}},
				additionalProperties: false
			}
		}
	},
	dispatches: {
		/**
		 * Dispatches when the expanded state of the
		 * accordion changes.
		 * @type {{value: boolean, componentId: string}}
		 * @uib.description Dispatches when the expanded state of the accordion item changes
		 */
		'NOW_ACCORDION_ITEM#EXPANDED_SET': {
			schema: {
				type: 'object',
				properties: {
					value: {type: 'boolean'},
					componentId: {type: 'string'}
				}
			}
		}
	},
	slots: {
		/**
		 * Slot for an identifier icon to the left of the trigger header.
		 * For example, `<now-icon icon="building-fill" slot="identifier" />`
		 */
		identifier: {},
		/**
		 * Slot for a metadata element to the right
		 * of the trigger and left of the trigger icon.
		 * For example, `<now-highlighted-value slot="metadata" />`
		 */
		metadata: {},
		/**
		 * Slot for accordion item content.
		 * For example, `<div slot="content">Hello world!</div>`
		 */
		content: {}
	},
	initialState: {
		headerRef: null,
		captionRef: null
	},
	eventHandlers: [
		{
			events: ['click'],
			modifier: {name: 'throttle', limit: 300},
			effect({host, properties, dispatch, action, updateProperties}) {
				const {
					payload: {event},
					meta: {id}
				} = action;

				if (properties.disabled) {
					action.preventDefault();
					action.stopPropagation();
					return;
				}

				const {shadowRoot} = host;
				const trigger = shadowRoot.querySelector('.now-accordion-item-button');
				const path = event.composedPath();

				if (!path.includes(trigger)) return;

				const collapse = shadowRoot.querySelector('#accordion-item');
				const value = !collapse.expanded;

				updateProperties({expanded: value});

				dispatch(() => ({
					type: 'NOW_ACCORDION_ITEM#EXPANDED_SET',
					payload: {value, componentId: id}
				}));
			}
		}
	],
	actionHandlers: {
		[COMPONENT_RENDERED]: ({host, updateState}) => {
			const headerRef = host.shadowRoot.querySelector(
				'.now-accordion-item-header .now-accordion-item-label'
			);
			const captionRef = host.shadowRoot.querySelector(
				'.now-accordion-item-caption .now-accordion-item-label'
			);

			updateState({headerRef, captionRef});
		}
	},
	behaviors: [
		{behavior: focusBehavior},
		{behavior: rtlBehavior},
		{
			behavior: resizeBehavior,
			options: {timer: {type: 'throttle', wait: 100}}
		}
	],
	styles,
	view
});
