import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import rtlBehavior from '@servicenow/behavior-rtl';
import focusBehavior from '@servicenow/behavior-focus';
import {getActiveElement} from '@servicenow/library-enhanced-utils';
import styles from './_now-accordion.scss';
import {triggerIconSchema, headingLevelSchema} from './schemas';

const {COMPONENT_RENDERED} = actionTypes;
const trapKeys = ['ArrowUp', 'ArrowDown', 'Home', 'End'];
const ACCORDION_ITEM_SELECTOR = ':scope > now-accordion-item';

/**
 * A collapsible component that reduces the need to scroll
 * when presenting multiple sections of content on a single page.
 *
 * `now-accordion` passes these properties to all item sub-components:
 * - `trigger-icon`: Changes the type and position of the trigger icon.
 * - `hide-dividers`: When true, removes dividers between items.
 * - `expand-single`: When true, allows only one item to expand at a time.
 * - `heading-level`: When defined, sets a custom heading level. All items
 * 	use the same level.
 *
 * ```jsx
 * <now-accordion
 * 	expand-single
 * 	heading-level="2"
 * 	toggle-icon={{ type: 'plus-minus', position: 'start' }}>
 * 	<now-accordion-item header="Step 1">
 * 		...
 * 	</now-accordion-item>
 * 	<now-accordion-item header="Step 2">
 * 		...
 * 	</now-accordion-item>
 * </now-accordion>
 * ```
 *
 * @seismicElement now-accordion
 * @summary Gives users the ability to reveal and hide relevant sections of content.
 * @uib.label Accordion
 * @uib.icon accordion
 * @uib.description Gives users the ability to reveal and hide relevant sections of content.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties expandSingle, headingLevel, triggerIcon, hideDividers
 */
createCustomElement('now-accordion', {
	properties: {
		/**
		 * When true, allows only one `now-accordion-item` sub-component to expand at a time.
		 *
		 * Default: `false`
		 * @type {boolean}
		 * @uib.description When true, allows only one `now-accordion-item` sub-component to expand at a time
		 */
		expandSingle: {default: false, schema: {type: 'boolean'}},
		/**
		 * Configures the element that wraps each `now-accordion-item` sub-component trigger.
		 * If this property is set, the provided heading level tag wraps
		 * the trigger per WAI-ARIA accessibility guidelines. For accessibility,
		 * consider the heading levels on the entire page when choosing
		 * the item's heading level.
		 *
		 * @type {'1'|'2'|'3'|'4'|'5'|'6'}
		 * @uib.description drives the heading level of each accordion items heading accordign to WAI-ARIA
		 * @uib.defaultValue 3
		 */
		headingLevel: {required: true, schema: headingLevelSchema},
		/**
		 * Configures the type and position of each `now-accordion-item` sub-component
		 * trigger icon.
		 *
		 * Options:
		 * - `[triggerIcon.type]`: String. Configures the icon-type to use. Default: 'chevron'.
		 * - `[triggerIcon.position]`: String. Configures the position of the trigger icon. Default: 'end'.
		 * @type {{type: ('chevron'|'plus-minus'), position: ('start'|'end')}}
		 * @uib.description Configures the type ("chevron" or "plus-minus") and position ("start" or "end") of each accordion item trigger icon
		 * @uib.defaultValue {"type": "chevron", "position": "start"}
		 */
		triggerIcon: {schema: triggerIconSchema},
		/**
		 * Hides divider lines between `now-accordion-item` sub-components.
		 * @type {boolean}
		 * @uib.description Hides divider lines between accordion items
		 */
		hideDividers: {default: false, schema: {type: 'boolean'}}
	},
	slots: {
		/**
		 * The default slot to add content to
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect({host, action}) {
				const {event} = action.payload;
				if (!trapKeys.includes(event.key)) return;
				const items = Array.apply(
					null,
					host.querySelectorAll(ACCORDION_ITEM_SELECTOR)
				).filter((item) => !item.disabled);
				const activeElement = getActiveElement();
				let triggerHasFocus = false;

				const triggers = items.map((item) => {
					const trigger = item.shadowRoot.querySelector(
						'.now-accordion-item-button'
					);
					if (trigger === activeElement) triggerHasFocus = true;
					return trigger;
				});

				// Don't allow focus trapping with arrow
				// keys unless a trigger has focus
				if (!triggerHasFocus) return;

				const [ARROW_UP, ARROW_DOWN, HOME, END] = trapKeys;
				const firstTrigger = triggers[0];
				const lastTrigger = triggers[triggers.length - 1];
				const isArrowDown = event.key === ARROW_DOWN;
				const isArrowUp = event.key === ARROW_UP;

				if (
					(isArrowDown && activeElement === lastTrigger) ||
					event.key === HOME
				) {
					action.preventDefault();
					firstTrigger.focus();
				} else if (
					(isArrowUp && activeElement === firstTrigger) ||
					event.key === END
				) {
					action.preventDefault();
					lastTrigger.focus();
				} else {
					action.preventDefault();
					const triggerIndex = triggers.indexOf(activeElement);

					isArrowDown
						? triggers[triggerIndex + 1].focus()
						: triggers[triggerIndex - 1].focus();
				}
			}
		}
	],
	actionHandlers: {
		[COMPONENT_RENDERED]: ({
			host,
			properties: {hideDividers, triggerIcon, headingLevel},
			action: {
				payload: {previousRenderProperties}
			}
		}) => {
			const items = host.querySelectorAll(ACCORDION_ITEM_SELECTOR);

			// Apply global properties for heading level, trigger-icon, and divider presence

			items.forEach((item, index) => {
				if (previousRenderProperties.hideDividers !== hideDividers) {
					if (hideDividers) {
						item.dividers = {};
					} else {
						item.dividers =
							index === 0 ? {top: true, bottom: true} : {bottom: true};
					}
				}

				if (previousRenderProperties.headingLevel !== headingLevel) {
					item.headingLevel = headingLevel;
				}

				if (previousRenderProperties.triggerIcon !== triggerIcon) {
					item.triggerIcon = triggerIcon;
				}
			});
		},
		'NOW_ACCORDION_ITEM#EXPANDED_SET': ({
			host,
			properties: {expandSingle},
			action
		}) => {
			if (!expandSingle) return;

			const {componentId} = action.payload;
			const items = host.querySelectorAll(ACCORDION_ITEM_SELECTOR);

			items.forEach((item) => {
				if (
					item.getAttribute('component-id') === componentId ||
					item.expanded === false
				) {
					return;
				}

				item.expanded = false;
			});
		}
	},
	behaviors: [{behavior: focusBehavior}, {behavior: rtlBehavior}],
	styles,
	view: () => <slot />
});
